import React from "react";
import style from "../css/sideMenu.module.css";
import { Link, useLocation, useRouteMatch, withRouter } from "react-router-dom";
import { logo } from "../../Assets";
const NavLink = ({ to, ...rest }) => {
  let location = useLocation();

  const isActive = location.pathname === to;
  return (
    <Link
      to={to}
      {...rest}
      className={
        isActive
          ? `${style.Sidemanu_li_Link} ${style.active}`
          : `${style.Sidemanu_li_Link} `
      }
    />
  );
};

function Side_menu() {
  const { url } = useRouteMatch();
  console.log(url);
  return (
    <div className={style.sidemenuWrapper}>
      <div className={style.header}>
        <NavLink to='/'>
          {" "}
          {/* <img src={logo} alt="PlatformX" /> */}{" "}
          <img
            src='https://1000logos.net/wp-content/uploads/2021/10/logo-Meta.png'
            alt=''
            srcSet=''
          />
        </NavLink>{" "}
      </div>{" "}
      <div className={style.menuContent}>
        <ul>
          <li className={style.Sidemanu_li}>
            <NavLink to='/Console/'> Create Document </NavLink>{" "}
          </li>{" "}
          <li className={style.Sidemanu_li}>
            <NavLink to='/Console/tab1'> Bientôt disponible... </NavLink>{" "}
          </li>{" "}
          <li className={style.Sidemanu_li}>
            <NavLink to='/Console/tab2'> Bientôt disponible... </NavLink>{" "}
          </li>{" "}
          <li className={style.Sidemanu_li}>
            <NavLink to='/Console/tab3'> Bientôt disponible... </NavLink>{" "}
          </li>{" "}
          <li className={style.Sidemanu_li}>
            <NavLink to='/Console/tab4'> Bientôt disponible... </NavLink>{" "}
          </li>{" "}
        </ul>{" "}
      </div>{" "}
    </div>
  );
}

export default withRouter(Side_menu);
